@media (min-width: 320px) {
    .left {
        padding: 5px;

    }

    .right {
        padding: 30px;
    }

    .container {
        padding: 1px;
        text-align: center;
    }

    .cajain {
        padding: 0px 50px 0px 50px;
    }

    .name {
        width: 150px;
        text-align: start;
    }

    .buton {
        min-width: 50%;
    }

    .nam2 {
        padding: 10px 0px 0px 0px;
    }
}



@media (min-width: 768px) {
    .left {
        padding: 0px 0px 0px 0px;
    }

    .right {
        padding: 25px
    }

    .cajain {
        padding: 0px 25px 0px 25px;
    }

    .name {
        width: 150px;
        text-align: start;
    }

    .buton {
        min-width: 35%;
    }
}

@media (min-width: 1024px) {
    .in {
        width: 60px;
        text-align: center;
    }

    .col-2 {
        padding: 0px;
        width: 60px;
    }

    .left {
        padding: 0px 50px 20px 0px;
    }

    .right {
        padding: 20px 0px 20px 10px;
    }

    .name {
        width: 200px;
        text-align: start;
    }

    .buton {
        min-width: 25%;
    }

    .nam {
        padding: 0px 0px 0px 100px;

    }

    .nam1 {
        padding: 0px 100px 0px 0px;

    }
}